import React from 'react';
import { Card, Grid, Section } from 'components';

// ----------------------------------------------------------

const experiencesIfro = [
  {
    company: 'Instituto Federal de Rondnia',
    role: 'Monitor',
    period: 'Nov 2021 - Dez 2022',
    location: 'Ji-Paran/RO',
    description: 'Projeto ofertado pelo MEC. Atuei como monitor para ensinar Lgica e Linguagem de Programao bsica para alunos de Escolas Municipais e Pblicas.'
  },
  {
    company: 'Instituto Federal de Rondnia',
    role: 'Desenvolvedor Pleno / Tech Lead',
    period: 'Jan 2022 - Jul 2024',
    location: 'Ji-Paran/RO',
    description: 'Desenvolvimento da plataforma web PROINFE para gerenciamento acadmico utilizando Next.js com React e Nest.js. Trabalho em equipe com metodologias geis para entregas eficientes e de alta qualidade.'
  },
  {
    company: 'Instituto Federal de Rondnia',
    role: 'Desenvolvedor Pleno',
    period: 'Jul 2023 - Atual',
    location: 'Ji-Paran/RO',
    description: 'Desenvolvimento de sistema para gerenciamento transparente das filas de espera por vagas em creches em Rondnia. Utilizao de Next.js e servios de geolocalizao da Google Cloud (Maps, Routes, Places). Colaborao ativa com equipe seguindo metodologia gil.'
  },
  
];

const experiencesWork = [
  {
    company: 'Softlutions',
    role: 'Desenvolvedor Pleno',
    period: 'Set 2022 - Presente',
    location: 'Ji-Paran/RO',
    description: 'Desenvolvimento front-end com micro front-ends, gerenciamento de servidores AWS EC2 e Cloudflare',
    project: 'https://lasercinemas.com.br/'
  }
];

export const Projects = React.forwardRef((_, ref) => {
  return (
    <Section ref={ref as React.LegacyRef<HTMLElement>} id="projetos">
      <h2>Projetos e Experincias</h2>
      <Grid>
        {experiencesWork.map((exp, index) => (
          <Card key={index}>
            <h3>{exp.company} - {exp.role}</h3>
            <p>{exp.period} | {exp.location}</p>
            <p>{exp.description}</p>
            <a style={{color:'blue'}}>{exp.project}</a>
          </Card>
        ))}
      </Grid>
     
        {experiencesIfro.map((exp, index) => (
           <Grid>
          <Card key={index}>
            <h3>{exp.company} - {exp.role}</h3>
            <p>{exp.period} | {exp.location}</p>
            <p>{exp.description}</p>
          </Card>
          </Grid>
        ))}
    </Section>
  );
});
