import React from 'react';
import { Card, Grid, Section } from 'components';

// ----------------------------------------------------------


export const Education = React.forwardRef((_, ref) => {
  return (
    <Section ref={ref as React.LegacyRef<HTMLElement>} id="formacao">
      <h2>Formao Acadmica e Certificaes</h2>
      <Grid>
        <Card>
          <h3>Tcnico em Informtica</h3>
          <p>IFRO - Concludo em 2022</p>
        </Card>
        <Card>
          <h3>Anlise e Desenvolvimento de Sistemas</h3>
          <p>IFRO - Previso de concluso 2025</p>
        </Card>
      </Grid>
    </Section>
  );
});
